.calendar-type {
    padding: 6px;
    cursor: pointer;
    transition: 0.3s all;
    display: block;
    @include r(1200) {
        display: none;
    }
    &:hover {
        opacity: 0.7;
    }
    &__radio {
        display: none;
        visibility: hidden;
        &:checked + .calendar-type__icon {
            fill: $accent;
        }
    }
    &__icon {
        fill: #BDBDBD;
        display: block;
        width: 23px;
        height: 20px;
    }
}

.calendar-cart {
    display: block;
    color: $black;
    padding: 10px 11px 20px 11px;
    width: calc(25% - 10px);
    text-decoration: none;
    background-color: $light;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 20px;
    border: 1px solid #D1D9DD;
    @include r(1200) {
        width: calc(50% - 10px);
    }
    @include r(450) {
        width: 100%;
    }
    &:hover {
        box-shadow: 0px 0px 30px rgba(23, 66, 84, 0.1);
    }
    &--clear {
        max-height: 0px;
        padding: 0;
        margin: 0;
        visibility: hidden;
        overflow: hidden;
    }
    &__thumb {
        display: block;
        margin-bottom: 28px;
        width: 100%;
        height: auto;
    }
    &__title {
        font-weight: 500;
