        margin-bottom: 15px;
        font-weight: 300;
        font-size: 14px;
        flex-grow: 1;
    }
    &__type {
        color: $accent;
        font-size: 14px;
    }
    &__icon {
        align-self: center;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 45px;
        height: 45px;
        border-radius: 5px;
        background-color: $red;
        svg {
            width: 24px;
            height: 24px;
            display: block;
        }
    }
}