.text {
	line-height: 1.5;
	& :first-child {
		padding-top: 0;
		margin-top: 0;
	}
	& :last-child {
		 padding-bottom: 0;
		 margin-bottom: 0;
	}

	&--default {
		& :first-child {
			padding-top: 1em;
		}
		& :last-child {
			padding-bottom: 1em;
		}
	}
	&--18 {
		font-size: rem(18px);
		@include r(1200) {
			font-size: rem(16px);
		}
	}
	&--20 {
		font-size: rem(20px);
		@include r(1200) {
			font-size: rem(14px);
		}
	}
	&--24 {
		font-size: rem(24px);
		@include r(1200) {
			font-size: rem(16px);
		}
	}
	&--14 {
		font-size: rem(14px);
	}
	&--grey {
		p {
			color: #58595B;
		}
	}
	&--regular {
		font-weight: 400;
	}
	
	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		padding-top: 1em;
		padding-bottom: 1em;
		@include r(1200) {
			padding-top: 0.7em;
			padding-bottom: 0.7em;
		}
		strong {
			color: $red;
		}
	}
	h3 {
		padding-top: 0;
		font-weight: 500;
		font-size: rem(36px);
		line-height: rem(42px);
		@include r(1200) {
			font-size: 26px;
			line-height: 1.3;
		}
	}
	h4 {
		font-weight: 500;
		font-size: rem(24px);
		@include r(1200) {
			font-size: 20px;
			line-height: 1.3;
		}
	}
	h5 {
		font-weight: 500;
		font-size: rem(18px);
	}
	h6 {
		font-weight: 500;
	}
	p {
		margin-bottom: 1.5em;
		@include r(1200) {
			margin-bottom: 1em;
		}
	}
	strong {
		font-weight: bold;
		color: #c39367;
	}
	b {
		font-weight: bold;
		color: $black;
	}
	ul {
		margin-bottom: 1.5em;
		@include r(1200) {
			margin-bottom: 1em;
		}
		li {
			position: relative;
			padding-left: rem(32px);
			margin-bottom: 1em;
			@include r(1200) {
				padding-left: rem(22px);
			}
			&:before {
				width: 17px;
				height: 1px;
				background-color: $accent;
				display: block;
				position: absolute;
				left: 0px;
				content: "";
				top: 0.5em;
				@include r(1200) {
					width: rem(12px);
				}
			}
		}
	}
	ol {
		list-style-type: decimal;
		// color: $red;
		list-style-position: inside;
		li {
			margin-bottom: 1em;
			// font-weight: bold;
			&::maker {
				font-weight: bold;
			}
		}
		span {
			font-weight: 300;
			color: $black;
			padding-left: 12px;
		}
	}
	a {
		color: $black;
		text-decoration: underline;
		&:hover {
			text-decoration: none;
		}
	}
	strong {
		a {
			color: $red;
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
	img {
		display: block;
		margin-bottom: 1rem;
		width: 100%;
		height: auto;
	}
	iframe {
		display: block;
		width: 100%;
		margin-bottom: 2rem;
		@include r(1200) {
			margin-bottom: 1.2rem;
		}
	}
	br {
		@include r(1200) {
			display: none;
			visibility: hidden;
		}
	}
	.icon-svg {
		display: block;
		height: 36px;
		width: 36px;
		margin-bottom: 1rem;
	}
	.btn {
		padding-left: rem(20px);
		padding-right: rem(20px);
		margin-bottom: 2.5rem;
		margin-top: 1rem;
		svg {
			margin-bottom: 0px;
			height: 1rem;
			width: 1rem;
		}
	}
	&--center {
		text-align: center;
	}
}

