	&--light {
	}
	&--dark {
		b {
			color: $light;
		}
	}
}

.table {
	margin-bottom: rem(50px);
	&--normal {
		table {
			width: 100%;
			table-layout: fixed;
		}
	}
	@include r(1200) {
		margin-bottom: rem(30px);
		overflow-x: scroll;
	}
	&__discript {
		font-size: 14px;
	}
	table {
		width: 100%;
		text-align: left;
		border-collapse: collapse;
		margin-bottom: 1rem;
	}
	td {
		background-color: $light;
	}
	th {
		background-color: $red;
		color: $light;
		text-align: center;
		vertical-align: middle;
		// &:first-child {
		// 	min-width: rem(100px);
		// }
	}
	th,
	td {
